.quad-chart-tooltip {
    font-size: 12px;
    line-height: 1.5;
    text-align: center;
    position: absolute;
    padding: 8px;
    background-color: rgba(0, 0, 0, 0.7); 
    color: white;
    border-radius: 4px;
    pointer-events: none;
    opacity: 0; 
    z-index: 1000;
    transition: opacity 0.2s ease-in-out;
}
